
import { useState } from "react";
import React from "react";
import { Container } from "react-bootstrap";

const CompoundInterest = () => {
    const [principal, setPrincipal] = useState();
    const [years, setYears] = useState();
    const [interest, setInterest] = useState();
    const [time, setTime] = useState();
    const [mvalue, setResult] = useState();
    const [iamount, setAmount] = useState();

    const calculate = () => {
        // A=p(1+(r/n))^(nt)
        const result = (principal * Math.pow((1 + (interest/100)), (time)));
        setResult(result.toFixed(2));
    };

    return (
               <div className="container">
                <div className="row">
                    <div className="col-lg-8">
                        <form>
                            <div className="mb-3 row">
                                <label htmlFor="exampleFormControlInput1" className=" col-sm-4 calc-label">Investment amount </label>
                                <div className="col-sm-8">
                                    <input onChange={(e) => setPrincipal(e.target.value)} type="number" className="calc-control" id="exampleFormControlInput1" placeholder="" />
                                </div>
                            </div>
                            <div className="mb-3 row">
                                <label htmlFor="exampleFormControlInput1" className="col-sm-4 calc-label">Rate of Interest Annual in %</label>
                                <div className="col-sm-8">
                                    <input onChange={(e) => setInterest(e.target.value)} type="number" className="calc-control" id="exampleFormControlInput1" placeholder="" />
                                </div>
                            </div>
                            <div className="mb-3 row">
                                <label htmlFor="exampleFormControlInput1" className=" col-sm-4 calc-label">Time in Years</label>
                                <div className="col-sm-8">
                                    <input onChange={(e) => setTime(e.target.value)} type="number" className="calc-control" id="exampleFormControlInput1" placeholder="" />
                                </div>
                            </div>

                            <button onClick={() => { calculate(); }} type="button" className="btn btn-primary">Calculate</button>
                        </form>
                    </div>
                    <div className="col-lg-4">
                        <div className="pricipalDiv">
                            <h3>Details</h3>
                            <h4>Future Value:  {mvalue}</h4>
                            <h4>Rate of Interest: {interest}%</h4>

                        </div>
                    </div>
                </div>
                </div>
    )
}

export default CompoundInterest