import React, { Component } from 'react'
import axios from 'axios';

export class NApi extends Component {

  constructor() {
    super()

    this.state = {
      items: [],
      isLoaded: false
    }
  }

  componentDidMount() {
    const url = 'https://api.nivesh.com/API/GetSummaryDetails';
    const postBody = {
      TotalClient: "",
      TotalPartner: "",
      TotalPartnerOverCities: "",
      NoOfTransaction: "",
      TotalTransactionValue: ""

    };
    const requestMetadata = {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'token': '636F8F63-06C4-4D95-8562-392B34025FB0'
      },
      body: JSON.stringify(postBody)
    };

    fetch(url, requestMetadata)
      .then(response => response.json())
      .then(data => {
        this.setState({ isLoaded: true, items: data.SummaryDetailsList });
      }).catch(error => {
        console.log(error);
      });
  }

  render() {
    const { isLoaded, items } = this.state;
    if (!isLoaded) return <div>Loading...</div>;
    return (
      <div id="about-stats">
        <div className="container container-wrapper">

          {items && (
            <div className="row row justify-content-center align-items-center">
              <div className="col-lg-4 col-md-6 col-12 mb-4">
                <div className="card-box">
                  <h3>{(items.TotalClient).toLocaleString('en-IN', {
                    maximumFractionDigits: 0
                  })}</h3>
                  <h4 className='text-white'>Total Clients</h4>
                </div>
              </div>
              <div className="col-lg-4 col-md-6 col-12 mb-4">
                <div className="card-box">
                  <h3> {(items.TotalPartner).toLocaleString('en-IN', {
                    maximumFractionDigits: 0
                  })}</h3>
                  <h4 className='text-white'>Total Partners</h4>
                </div>
              </div>
              <div className="col-lg-4 col-md-6 col-12 mb-4">
                <div className="card-box">
                  <h3>{(items.TotalPartnerOverCities).toLocaleString('en-IN', {
                    maximumFractionDigits: 0
                  })}</h3>
                  <h4 className='text-white'>Total Partner Over Cities</h4>
                </div>
              </div>
              <div className="col-lg-4 col-md-6 col-12 mb-4">
                <div className="card-box">
                  <h3>{(items.NoOfTransaction).toLocaleString('en-IN', {
                    maximumFractionDigits: 0
                  })}</h3>
                  <h4 className='text-white'>Number of Transactions</h4>
                </div>
              </div>
              <div className="col-lg-4 col-md-6 col-12 mb-4">
                <div className="card-box">
                  <h3>Rs. {parseInt(items.TotalTransactionValue).toLocaleString('en-IN', {
                    maximumFractionDigits: 0
                  })} Cr</h3>
                  <h4 className='text-white'>Total Transaction Value</h4>
                </div>
              </div>
            </div>

          )}

        </div>
      </div>


    )
  }
}

export default NApi
