import React, { Component } from 'react'
import SecondAPI from '../../Component/SecondAPI/SecondAPI';
import pimg from "../Home/images/Group 7259.svg"
import Shriram from '../Home/images/Shriram Transport Finance Company Ltd/1.png'
import ShriramF1 from '../Home/images/Shriram Transport Finance Company Ltd/Attractive Returns on FD.svg'
import ShriramF2 from '../Home/images/Shriram Transport Finance Company Ltd/Flexible Tenure.svg'
